import React, {createRef, useRef} from "react";
import {
  AppBar,
  Avatar,
  CssBaseline,
  Drawer,
  IconButton,
  List,
  ListItem,
  Toolbar,
  Typography,
  useTheme
} from "@material-ui/core";
import {Add, AllInclusiveOutlined, DashboardOutlined, SettingsOutlined, EventOutlined} from "@material-ui/icons";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {useHistory} from "react-router";
import {useSelector} from "react-redux";
import TopMenu from "./TopMenu";
import drawerConfig from "../config/drawer";
import {colors} from "../utils/colors";

const drawerWidth = 80;
const useStyles = makeStyles(theme => ({
  root: {
    display: "flex"
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0
  },
  drawerPaper: {
    width: drawerWidth
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3)
  },
  toolbar: theme.mixins.toolbar,
  drawerContent: {
    marginTop: 100
  }
}));

const Template = ({ selected, children, ...props }) => {
  const classes = useStyles();
  const history = useHistory();
  const theme = useTheme()
  const ref= useRef()
  const {user} = useSelector(state => state.auth)
  return (
    <div className={classes.root} {...props}>
      <CssBaseline />
      <TopMenu ref={ref} />
      <AppBar
        className={classes.appBar}
        variant={"elevation"}
        position={"fixed"}
      >
        <Toolbar>
          <AllInclusiveOutlined fontSize={"large"} />
          <Typography style={{ marginLeft: 10 }} variant={"h6"}>
            Chalo Chalen
          </Typography>
          <span style={{ flexGrow: 1 }} />
          <IconButton onClick={event => ref.current.open(event)}>
            <Avatar src={user.profile_picture} className={classes.avatar} />
          </IconButton>
        </Toolbar>
      </AppBar>
      <Drawer
        className={classes.drawer}
        variant="permanent"
        classes={{
          paper: classes.drawerPaper
        }}
      >
        <List className={classes.drawerContent}>
          {
            drawerConfig.items.map(item=> (
              <ListItem
                key={item.title}
                onClick={() => history.push(item.route)}
                button
                style={{ display: "flex", justifyContent: "center", height: 60 }}
              >
                {
                  <item.icon
                    fontSize="large"
                    style={{
                      color:
                        item.title === selected
                          ? colors.blue
                          : theme.palette.grey.A700,
                    }}
                  />
                }
              </ListItem>
            ))
          }
        </List>
      </Drawer>
      <main className={classes.content}>
        <div className={classes.toolbar} />
        {children}
      </main>
    </div>
  );
};

export default Template;
